import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileHttpH2CListResponse } from "../../Model/Response/FileHttpH2CListResponse";
import { FileHttpH2CResponse } from "../../Model/Response/FileHttpH2CResponse";
import { FileHttpH2CViewModel } from '../../Model/ViewModel/FileHttpH2CViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileHttpH2CServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileHttpH2CServicesProvider extends FileServicesProvider {
  private FileHttpH2Cs: FileHttpH2CListResponse;
  private FileHttpH2C: FileHttpH2CResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileHttpH2C";
    console.log("Hello FileHttpH2CServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileHttpH2C?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileHttpH2C/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileHttpH2Cs = new FileHttpH2CListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileHttpH2Cs;
    });
  }



  public PostPutAsync(FileHttpH2CViewModel:FileHttpH2CViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileHttpH2C";
    else
    sub_url = "FileHttpH2C/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileHttpH2CViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileHttpH2CViewModel,mode).map(
      item => {
        this.FileHttpH2C = new FileHttpH2CResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileHttpH2C;
      }
    );
  }
}



